.wallet-button {
  color: rgba(var(--cl-c),1.0);
  width: 210px;
  height: 51px;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 10px;
 
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 100%;
  background: rgba(var(--bg-a),1.0);
  box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.08);
  border-radius: 22px;
}

.main-button {

  min-width: 210px;
  min-height: 65px;
  color: rgba(var(--cl-a),1.0);

  font-family: var(--font-family);
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 120%;
 
  box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.08);
  border-radius: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 5px;
  background-color: rgba(var(--bg-d),1.0);
  transition: all .4s;

  &--transparent {
    background-color: transparent;
    color: rgba(var(--cl-b),1.0);
    border: 1px solid var(--br-a);
  }

  &--mode{
    border-radius: 12px;
    min-height: 55px;
  }
  
  svg{
    fill: currentColor;
  }
}

.main-label{
  font-family: var(--font-family2);
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 100%;
  color:  rgba(var(--cl-a),1.0);
  background-color: rgba(var(--bg-d),1.0);
  border-radius: 22px;
  padding: 8px 20px;
}

p{
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 120%;
  color:  rgba(var(--cl-e),1.0);

}

.main-title{
  font-family: var(--font-family2);
  font-style: normal;
  font-weight: 700;
  font-size: 50px;
  line-height: 115%;
  text-align: center;
  text-transform: uppercase;
  color:  rgba(var(--cl-b),1.0);
  max-width: 700px;
}

.pre-title{
  font-family: var(--font-family2);
  font-style: normal;
  font-weight: 700;
  font-size: clamp(26px, 3.636vw, 40px);
  line-height: 120%;
  text-transform: uppercase;
  color:  rgba(var(--cl-b),1.0);
}

.main-scroll{
  display: flex;
  align-items: center;
  grid-gap: 4px;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 100%;
  color: rgba(var(--cl-c),0.4);
  
  svg {
    fill: currentColor;
    transition: inherit;
  }
}

.main-description{
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;

  img,picture{
    width: 100%;
    display: block;
  }
} 

.card-top{
  &:before{
    position: absolute;
    content: "";
    background: rgba(255, 255, 255, 0.5);
    border-radius: 22px;
    width: 194px;
    height: 50px;
    top: -10px;
    left: 50%;
    transform: translateX(-50%);
    z-index: -1;
    pointer-events: none;
  }

  &--mode{
    &:before{
      width: calc(100% - 24px);
    }
  }
}

.violet-text{
  font-size: inherit;
  font-family: inherit;
  color: rgba(var(--cl-d),1.0);
}